.container-access {
  max-width: 320px;

  @media screen and (min-width: 768px) {
    max-width: 768px;
  }

  @media screen and (min-width: 1280px) {
    max-width: 1280px;
  }
}

.accessories {
  overflow: hidden;
  position: relative;
  padding: 144px 0 449px 0;
  margin: auto;
  // max-width: 320px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  // height: 3198px;
  position: relative;
  overflow: hidden;
  padding: 144px 0 48px 0;
  margin: auto;
  // max-width: 320px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  // height: 3198px;

  background-image: url(/src/images/accessories/back-access-mobile.jpg);

  @media screen and (min-device-pixel-ratio: 2),
  screen and (min-resolution: 192dpi),
  screen and (min-resolution: 2dppx) {
    background-image: url(/src/images/accessories/back-access-mobile_2x.jpg);
  }

  @media screen and (min-width: 768px) {
    // max-width: 768px;
    padding: 256px 0 120px 0;
    background-image: url(/src/images/accessories/back-access-tablet.jpg);

    @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
      background-image: url(/src/images/accessories/back-access-tablet_2x.jpg);
    }
  }

  @media screen and (min-width: 1280px) {
    // max-width: 1280px;
    background-size: cover;

    background-image: url(/src/images/accessories/back-access-desktop.jpg);

    @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
      background-image: url(/src/images/accessories/back-access-desktop_2x.jpg);
    }
  }

  &__filter {
    display: flex;
    gap: 24px;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 32px;

    @media screen and (min-width: 768px) {
      gap: 53px;
      margin-bottom: 72px;
    }

    @media screen and (min-width: 1280px) {
      gap: 44px;
    }
  }

  // &__item {}

  &__button {
    cursor: pointer;
    border: 1px solid #86e543;
    border-radius: 16px;
    background-color: transparent;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.19;
    color: #86e543;
    min-width: 124px;
    padding: 8px 0;

    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
      background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
      box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
      box-shadow: 0px 4px 10px rgba(134, 229, 67, 0.1);
    }

    &:active {
      box-shadow: 0px 4px 20px rgba(134, 229, 67, 0.4);
      background-color: #86e543;
      color: #01383a;
    }

    @media screen and (min-width: 768px) {
      min-width: 132px;
      padding: 16px 0;
    }

    @media screen and (min-width: 1280px) {
      min-width: 168px;
    }
  }

  &__title {
    font-weight: 500;
    font-size: 24px;
    line-height: 1.7;
    color: $main-font-color;
    margin-bottom: 27px;

    @media screen and (min-width: 768px) {
      margin-left: 102px;
      margin-bottom: 51px;
      font-size: 56px;
      line-height: 1.18;
    }
  }

  &__list {
    margin-bottom: 24px;

    @media screen and (min-width: 768px) {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      margin-right: -38px;
      margin-bottom: 32px;
    }

    @media screen and (min-width: 1280px) {
      justify-content: space-between;
      margin: auto auto 32px auto;
    }
  }

  &__article {
    @media screen and (max-width: 767px) {
      &:nth-child(-n + 5) {
        margin-bottom: 16px;
      }

      &:nth-child(n + 7) {
        display: none;
      }
    }

    @media screen and (min-width: 768px) and (max-width: 1279px) {
      margin-right: 38px;

      &:nth-child(-n + 10) {
        margin-bottom: 24px;
      }

      &:nth-child(n + 13) {
        display: none;
      }
    }

    @media screen and (min-width: 1280px) {
      &:nth-child(-n + 12) {
        margin-bottom: 24px;
      }
    }
  }
}

.article {
  width: 287px;

  &__link {
    &:hover .shutter {
      transform: translateY(0);
    }

    &:hover .article__wrap {
      box-shadow: 0px 4px 4px rgba(134, 229, 67, 0.2);
    }
  }

  &__wrap {
    position: relative;
    overflow: hidden;
    border: 1px solid #86e543;
    border-radius: 12px;

    transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  // &__image {}

  &__footer {
    padding: 8px 16px 8px 8px;
  }

  &__title {
    font-weight: 500;
    font-size: 22px;
    line-height: 1.18;
    color: #86e543;
    margin-bottom: 8px;
  }

  &__text-one {
    font-size: 14px;
    line-height: 1;
    text-decoration: line-through;
    color: #8d9289;
  }

  &__text-two {
    font-weight: 500;
    font-size: 20px;
    line-height: 1;
    color: #86e543;
  }
}

.article__wrap-text {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

